import React, { Component, Fragment } from 'react'
import { Row, Col } from 'antd'
import { css } from 'glamor'
import Link from 'gatsby-link'
import logo from '../images/muhrusdi-logo-none.png'
import vapor from '../images/muhrusdi-vapor.png'

class Index extends Component {
  constructor(props) {
    super(props)
    this.state = {
      links: [
        {
          title: 'Github',
          link: 'https://github.com/muhrusdi'
        },
        {
          title: 'Linkedin',
          link: 'https://github.com/muhrusdi'
        },
        {
          title: 'Instagram',
          link: 'https://github.com/muhrusdi'
        },
        {
          title: 'Twitter',
          link: 'https://github.com/muhrusdi'
        },
        {
          title: 'Behance',
          link: 'https://github.com/muhrusdi'
        }
      ]
    }
  }
  render() {
    let animateVapor = css.keyframes('left', {
      '0%': {
        transform: 'translateX(310px)'
      },
      '100%': {
        transform: 'translateX(-220px)'
      }
    })

    let white = css({
      color: '#fff'
    })
    
    let backgroundBlack = css({
      background: '#000'
    })

    let textCenter = css({
      textAlign: 'center'
    })

    let vaporBackground = css({
      position: 'relative',
      ':after': {
        background: `url(${vapor}) no-repeat`,
        position: 'absolute',
        bottom: 0,
        left: 0,
        right: 0,
        height: 119,
        zIndex: 9999,
        content: '""',
        transform: 'translateX(310px)',
        animationName: animateVapor,
        animationDuration: '100s',
        animationIterationCount: 'infinite',
        animationTimingFunction: 'linear'
      }
    })
    return (
      <Fragment>
        <div>
          <Row type="flex" align="middle" justify="center" {...backgroundBlack} {
            ...css({
              height: '100vh'
            })
          }>
            <div {...textCenter}>
              <div {...vaporBackground} {...css({
                marginBottom: 30
              })}>
                <img {...css({
                  height: 150
                })} src={logo} alt=""/>
              </div>
              <div {...css({
                marginBottom: 33
              })}>
                <h1 {...white}>Muhammad Rusdi</h1>
                <p {...white} {...css({
                  fontSize: 18
                })}>Front End Engineer: Javascript, ReactJS, GatsbyJS.</p>
              </div>
              <div>
                <Row type="flex" gutter={20} justify="center">
                  {
                    this.state.links.map((link, i) => (
                      <Col>
                        <a href={link.link} {...css({
                          display: 'block',
                          border: '1px solid #e9305d',
                          padding: '6px 22px',
                          color: '#fff',
                          transition: 'background .3s ease',
                          ':hover': {
                            background: '#e9305d',
                            color: '#fff'
                          }
                        })}>{link.title}</a>
                      </Col>
                    ))
                  }
                </Row>
              </div>
            </div>
          </Row>
        </div>
      </Fragment>
    )
  }
}

export default Index


